// import ProductStoryBox from '@/components/product-story-box.vue'; //产品故事
<template>
	<!--产品故事 start-->
	<div class="ProductStory" @click="ddfd">
		  <a href="javascript:;" class="ProductStory_img">
				<img :src="vData.product.imgs_url&&vData.product.imgs_url[0]" alt="" class="ProductStory_bgImg" />
				<span class="ProductStory_imgTip">产品故事</span>
		  </a>
		  <p class="ProductStory_imgExp">
		  	  <a href="javascript:;">{{vData.content}}</a>
		  </p>
		  <p class="ProductStory_wordTip">
		  	 {{vData.user_profile.username}}·点评 {{vData.profile.title}}
		  </p>
		
	</div>
	<!--产品故事 end-->
</template>
<style scoped lang="less">
   @m:750/375;
	.ProductStory{
		  width: 100%;
		  margin-bottom: 30px*@m;
	}
	.ProductStory_img{
		  display:block;
		  width: 100%;
		  height: 377px*@m;
		  border-radius: 8px*@m;
		  // background: forestgreen;
		  overflow: hidden;
		  position: relative;
	}
	.ProductStory_bgImg{
		  width: 100%;
		  height: 100%;
		  object-fit:cover;
	}
	.ProductStory_imgExp{
		 margin: 10px*@m 0;
	}
	.ProductStory_imgExp a{
		  font-family: PingFangSC-Medium;
			font-size: 16px*@m;
			color: #0F2540;
			letter-spacing: 0;
			line-height: 18px*@m;
			text-decoration: none;
	}
	.ProductStory_wordTip{
		font-family: PingFangHK-Medium;
		font-size: 13px*@m;
		color: #B9B9B9;
		letter-spacing: 0;
		line-height: 18px*@m;
	}
	.ProductStory_imgTip{
		  font-family: PingFangSC-Medium;
			font-size: 12px*@m;
			color: #0F2540;
			letter-spacing: 0;
			line-height: 12px*@m;
			background: #FFFFFF;
      border-radius: 100px*@m;
      display: block;
      position: absolute;
      top: 10px*@m;
      left: 10px*@m;
      padding: 5px*@m 6px*@m;
	}
	
</style>

<script>
export default {
  name: 'ProductStory',
	props: ['vData'],
  data () {
    return {
    
    }
  },
  methods:{
  	  ddfd(){
  	  	   
  	  }
  },
  mounted(){
	
  }
  

}
</script>


